.filler{
    flex:1;
    height:calc(var(--midtext)*5);
    background:rgba(255, 255, 255, 0.493);
    font-size: var(--bigtext);
    font-weight: bold;

    display: flex;
    gap:20px;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 4px 0px rgb(128, 128, 128);
    margin-bottom:5px;
}

/*App*/

#outerWrap{
    background-color: rgb(255, 255, 247);
    min-width: 300px;

    
}
#innerWrap{
    display: flex;
    flex-direction: column;
    
    box-shadow: 0px 0px 100px black;

    width: 100vw;
    max-width: 1000px;

    min-height: 100vh;
    height:100%;


    margin-left: auto;
    margin-right: auto;

    background-image: url("../img/backGround.png");

    --midtext: clamp(12px,calc(1.5vw + 4px),20px);
    --bigtext: calc(var(--midtext)*1.6);
}

/*Navbar*/

#navbar{
    height:calc(var(--midtext)*5);
    background:rgb(255, 255, 255);
    font-size: var(--bigtext);
    font-weight: bold;

    display: flex;
    gap:20px;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 4px 0px rgb(128, 128, 128);
    margin-bottom:5px;
}

#navbar .link{
    border:solid black 2px;
    background-color:rgb(255, 255, 255);
    border-radius: 10px;
    padding:10px;
}

.listContainer{
    flex: 1 1;
    display: flex;
    gap:40px;
    align-items: center;
    justify-content: center;
    flex-direction: column;


}
/*Home*/

#home {

}

.descriptionWrap{
    display: grid;
    grid-template-columns: calc(var(--bigtext)*6) 1fr;
    grid-gap:20px;
    max-width: 600px;
}

.description{
    font-size:var(--midtext);
    text-align: center;
    color:rgb(43, 43, 43);
    border:solid rgb(43, 43, 43) 2px;
    background-color:rgb(255, 255, 255);
    padding:10px;

    display: flex;
    justify-content: center;
    align-items: center;
 }

#home .link{
    border:solid black 5px;
    background-color:rgb(255, 255, 255);
    border-radius: 20px;
    padding:var(--midtext);
    font-size: var(--bigtext);
    font-weight: bold;
    text-align: center;
    height:calc(var(--bigtext)*2);

    display: flex;
    justify-content: center;
    align-items: center;
}

/* PickPlay */
#pickPlay{

}

.pickPlay .link{
    border:solid rgb(34, 34, 34) 2px;
    background-color:rgb(255, 255, 255);
    border-radius: 20px;
    padding:var(--bigtext);
    font-size: var(--bigtext);
    font-weight: bold;
    text-align: center;
    height:calc(var(--bigtext)*2);

    display: flex;
    justify-content: center;
    align-items: center;
}

.list{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:20px;
}

.todo{
    box-shadow: 0px 0px 10px black;
    background-color:rgb(255, 255, 255);
    padding:var(--midtext);
    font-size: var(--bigtext);
    font-weight: bold;
    text-align: center;
    height:calc(var(--bigtext)*2);

    display: flex;
    justify-content: center;
    align-items: center;

}


/* Create */

#create{
     background:rgba(255, 255, 255, 0.61); 
}

#create .todo{
    display: flex;
    gap:10px;
    flex-wrap: wrap;
}

.imgWrap{
    width: 100%;

    text-align: center;

    display: flex;
    gap:10px;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.img{
    width: 90%;
    min-width: 320px;
    box-shadow: 0px 0px 15px black;
}

.point .link{
    background-color:rgb(255, 255, 255);
    padding:var(--midtext);
    font-size: var(--midtext);
    font-weight: bold;
    text-align: center;
    height:calc(var(--midtext)*2);

    display: flex;
    justify-content: center;
    align-items: center;
}

.inputwrap{
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.point{
    display: flex;
    border:solid black 2px;
}

.point .link:first-child{
    border-right: solid 2px black;
}
.point .link:last-child{
    border-left: solid 2px black
}

.point input{
    font-size:var(--midtext);
    height:100%;
    padding-left:10px;
}

.name{
    font-size:var(--midtext);
    height:100%;
    padding-left:10px;
}
/* EditPoint */
.pointImage{
    position: relative;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.image{
    width: 100%;
}
/* Game */
.game{
    background:rgba(255, 255, 255, 0.61); 
}
/* Icons */


.icon{
    position: absolute;
    width:30px;
    height:auto;
    transform:translate(-50%,-80%);

    visibility: hidden;
    left:0;
    top:0
}
